@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300&family=Lora:wght@400&family=Roboto:wght@100;300;500&display=swap);
.skeleton{display:block;background:#eee}.skeleton.circle{border-radius:100%}

html,body,div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,a,img,strong,small,dl,dt,dd,ol,ul,li,fieldset,form,input,button,label,legend,table,caption,tbody,tfoot,thead,tr,th,td{padding:0;margin:0}article,aside,figcaption,figure,video,audio,footer,header,hgroup,menu,nav,section{display:block}html{font-size:18px}body{font-family:'Noto Sans KR', sans-serif;font-weight:300;line-height:1;color:#000}img,fieldset,button{border:0}ol,ul{list-style:none}a{text-decoration:none;color:inherit}a,img{vertical-align:top}a,button,label,input,textarea{outline:0}a,button,label,input,select{-webkit-tap-highlight-color:transparent}button,label{cursor:pointer}button{border:0;background-color:transparent;font-family:inherit;font-size:inherit}#root{position:relative;padding:130px 0 30px}.container{position:relative;width:90%;max-width:1440px;margin:0 auto}#header{position:fixed;top:0;right:0;left:0;height:80px;z-index:100;background:#fff}#header .container{height:100%}#header h1{position:relative;top:0;left:0;display:flex;height:100%;font-weight:100;font-size:1.45rem;text-align:center;align-items:center;margin:auto;font-family:'Lora', serif}#header h1 a{margin:0 auto 4px}#header .category_wrap{position:absolute;top:50%;left:0;width:100%;height:20px;margin-top:-10px}#header .category_wrap h2{display:flex;height:100%;align-items:center}#header .category_wrap h2 a{font-size:0}#header .category_wrap h2 svg{width:20px;height:20px}#header .category_wrap ul{position:absolute;top:0;left:20px;display:flex;align-items:center;margin-left:2%}#header .category_wrap ul li{line-height:1.2;margin:0 3%;font-family:'Roboto', sans-serif}#header .category_wrap ul li:not(.active){color:rgba(0,0,0,0.5)}#header .category_wrap ul li:hover{color:rgba(0,0,0,0.7)}main{min-height:calc(100vh - 80px)}main article{padding-bottom:2%}main h1{font-family:'Roboto', sans-serif;font-size:2.23rem;font-weight:100;line-height:1.3;letter-spacing:-0.01em;margin-bottom:1%}main ul{display:flex;flex-flow:row wrap;margin:0 -1.5%}main li{position:relative;word-break:keep-all;box-sizing:border-box;flex-basis:22%;overflow:hidden;margin:0 1.5% 3%}main li img{display:block;width:100%}main li>div{position:absolute;left:0;right:0;bottom:0;background:#fff;border-bottom:1px solid #ccc;line-height:1.2;padding:5% 4%}main li .info{position:relative;line-height:1.2}main li .info span{display:block}main li .title{position:relative;font-size:1.56rem;margin-right:40px}main li .subtitle{color:#555;margin-top:2%}main li .date{position:absolute;top:10%;right:0;font-size:.89rem;font-weight:100;font-family:'Roboto', sans-serif}main li .anchor{position:absolute;top:40%;left:4%;right:4%;display:none}main li .anchor .btn{position:relative;display:block;width:100%;text-align:left;font-family:'Roboto', sans-serif;color:#fff;font-size:1.34rem;font-weight:500;line-height:30px}main li .anchor .btn:before{content:"";position:absolute;bottom:45%;left:0;width:100%;height:1px;background:rgba(255,255,255,0.7)}main li .anchor .btn:first-child{margin-bottom:5%}main li:hover img{-webkit-filter:blur(2px);filter:blur(2px)}main li:hover>div{top:0;border:0 none;background:rgba(0,0,0,0.6)}main li:hover .anchor{display:block}main li:hover .info span{color:#fff}footer{font-family:'Roboto', sans-serif;line-height:1.5;font-size:0.9em;text-align:center;color:#666}#modal{position:fixed;top:0;right:0;bottom:0;left:0;display:flex;z-index:100;justify-items:center;overflow-y:auto;padding:2.5% 0}#modal .dim{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,0.5)}#modal .move{position:fixed;top:50%;width:100px;height:100px;background:rgba(0,0,0,0.5);margin-top:-50px}#modal .move span{position:absolute;width:1px;height:50%;background:#fff}#modal .move span:first-child{top:0}#modal .move span:last-child{bottom:0}#modal .move.prev{left:0}#modal .move.prev span{left:30px}#modal .move.prev span:first-child{top:0;transform:rotate(45deg);transform-origin:0 100%}#modal .move.prev span:last-child{bottom:0;transform:rotate(-45deg);transform-origin:0 0}#modal .move.next{right:0}#modal .move.next span{right:30px}#modal .move.next span:first-child{transform:rotate(-45deg);transform-origin:0 100%}#modal .move.next span:last-child{transform:rotate(45deg);transform-origin:0 0}#modal .modal_popup{position:relative;width:90%;max-width:840px;background:#fff;box-sizing:border-box;margin:auto}#modal .modal_popup>div{padding:5% 3%}#modal header{position:relative;border-bottom:1px solid #ccc;line-height:1.4;padding-bottom:1.5%;margin-bottom:3%}#modal h1,#modal .subtitle{display:inline-block}#modal h1{font-size:2.12rem;font-weight:300;margin-right:0.7%}#modal .subtitle{color:#555;font-size:1.34rem}#modal .date{position:absolute;right:0;bottom:0;font-family:'Roboto', sans-serif;font-weight:100;margin-bottom:2%}#modal section{line-height:1.5}#modal .info{margin:3% 0}#modal .info li{position:relative;padding-left:10px;margin-bottom:2px}#modal .info li:last-child{margin-bottom:0}#modal .info li:before{position:absolute;top:50%;left:0;content:"";width:2px;height:2px;border-radius:100%;background:#000}#modal .img{display:flex;flex-flow:row wrap;margin:0 -1.5%}#modal .img li{position:relative;flex-basis:30.33%;margin:0 1.5%}#modal .img li .screenshot{position:relative;display:inline-block}#modal .img li .screenshot:after{content:"";position:absolute;top:0;right:0;bottom:0;left:0;border:1px solid rgba(0,0,0,0.1)}#modal .img li img{width:100%}#modal .link{color:#00A8FF;word-break:break-all}#modal .link:hover{text-decoration:underline}#modal .page{overflow:hidden;margin-top:0.5%}#modal .page li{position:relative;float:left;font-size:.84rem;padding-left:5px;margin-right:1%}#modal .page li:before{position:absolute;top:50%;left:0;content:"";width:1px;height:1px;background:#333}#modal .page li a{color:#333}#modal .page li a:hover{text-decoration:underline}#modal .close{position:absolute;top:0;right:0;width:10%;max-width:50px;margin:1.5% 1.5% 0 0}#modal .close:before{content:"";display:block;padding-top:100%}#modal .close span{position:absolute;top:50%;left:0;width:100%;height:1px;background:#000;margin-top:-3px}#modal .close span:first-child{transform:rotate(45deg)}#modal .close span:last-child{transform:rotate(-45deg)}#loading{position:absolute;top:50%;left:50%;width:50px;height:50px;border:3px solid #eee;border-top-color:#000;border-radius:100%;margin:-105px 0 0 -25px;-webkit-animation:loadingspin 1s ease-in-out infinite;animation:loadingspin 1s ease-in-out infinite}@-webkit-keyframes loadingspin{to{-webkit-transform:rotate(360deg)}}@keyframes loadingspin{to{-webkit-transform:rotate(360deg)}}@media screen and (min-width: 1200px) and (max-width: 1400px){html{font-size:16px}}@media screen and (max-width: 1200px){main li{flex-basis:30.33%}}@media screen and (max-width: 1000px){html{font-size:16px}#modal .move{display:none}}@media screen and (max-width: 800px){#header{padding-bottom:20px}#header .category_wrap ul{left:0;top:40px;margin-left:0}main article{margin-bottom:5%}main li{flex-basis:47%}}@media screen and (max-width: 500px){html{font-size:15px}main li .title{font-size:1.12rem}#modal header h1{font-size:1.67rem}#modal header .subtitle{display:block;font-size:1rem}}

